.header {
  height: 90px;
  background-color: #fff;
  line-height: 45px;
}
.header .header-top {
  text-align: right;
  border-bottom: 1px solid #1890ff;
}
.header .header-top button {
  margin-right: 30px;
  margin-left: 20px;
}
.header .header-bottom {
  display: flex;
}
.header .header-bottom .header-bottom-left {
  position: relative;
  flex: 1 1;
  text-align: center;
  font-size: 20px;
}
.header .header-bottom .header-bottom-left::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 75%;
  width: 0;
  height: 0;
  margin: auto;
  border-width: 20px;
  border-color: #fff transparent green;
  border-style: solid;
  border-bottom: none;
}
.header .header-bottom .header-bottom-right {
  flex: 3 1;
  text-align: right;
  margin-right: 30px;
}
.header .header-bottom .header-bottom-right img {
  width: 30px;
  height: 20px;
}
.header .header-bottom .header-bottom-right span {
  padding: 0 10px;
  vertical-align: middle;
}
